import lexicrea_img_Illustr_mise_au_travail from '../images/lexicrea_img_Illustr_mise_au_travail.png';

let pathPrefix = `/`



export default () => (
    <PageTransition>
    <div className="layoutblocksContainer">
        <Helmet>
            <meta charSet="utf-8" />
            <link rel="canonical" href="https://www.lexiquedelacreativite.com" />
            <title>Lexique de la Créativité - Demoiselles d'Avignon et Créativité</title>
            <meta name="description" content="Découvrez comment les 'Demoiselles d'Avignon' de Picasso ont influencé la perception de la créativité dans l'art moderne et au-delà." />

        </Helmet>

        <div className="layoutblocksheaderSidebar">
            <Header/>
        </div>

        <div className="layoutblocksbodyMain">
        <Cta/>
            <div className="section">

            <div className="row align-center padding5_top">
                    <div className="small-11 medium-8 columns padding5_top">
                        <p className="letterCapitalcontent">M</p>
                        <h2>Mosaïque</h2>
                        <h3>Etude de la Mosaïque Créative des Demoiselles d’Avigon (P.Picasso)</h3>
                        <p className="text-left content-project">A 25 ans, Picasso décide de s’attaquer au "bordel de son inspiration" (citation du peintre). Il le trouvera calle Avinyó, à Barcelone. Ce tableau est considéré comme une rupture stylistique et comme le point de départ du cubisme. La mosaïque créative et l’ensemble des éléments qu’un créatif va connecter afin de donner naissance à un œuvre nouvelle. </p>
                        <h4>Genèse</h4>
                        <p className="text-left content-project">Pour Picasso, en 1907, choisir un "problème" créatif signifiait trouver un thème qu'il pouvait transformer en style conceptuel. Le concept était donc une nouvelle représentation de l'espace et du temps, pour Picasso choisir un problème signifiait trouver une scène dans laquelle ce concept pouvait s'exprimer d'une manière complexe et esthétiquement satisfaisante (Miller).</p>
                        <p className="text-left content-project">Loin de toute considération esthétique, nous allons analyser quelle est la mosaïque créative qui compose cette œuvre. Cette analyse a été complétée par le livre d'Arthur I Miller, Einstein et Picasso.</p>
                        <p className="text-left content-project">Je profite de cet article pour réaffirmer la puissance de la peinture, îlot organique, mystérieux et ambigu dans un océan d'images univoques et lisses. Ce tableau a d'abord été accueilli froidement par ses amis (Matisse et Derain inclus), Picasso attendra 10 ans avant de l’exposer.</p>
                        <p className="text-left content-project">Dans ses “conversations avec Picasso” Brassaï note que Picasso lui avait un jour dit : </p>
                        <div className="row align-center">
                        <div className="small-11 medium-9 columns">
                            <h2 className="project-quote">« … Pourquoi croyez-vous que je date tout ce que je fais ? C’est qu’il ne suffit pas de connaître les œuvres d’un artiste. Il faut aussi savoir quand il les faisait, pourquoi, comment, dans quelles circonstances. Sans doute existera-t-il un jour une science, que l’on appellera peut-être « la science de l’homme », qui cherchera à pénétrer plus avant l’homme à travers de l’homme-créateur… Je pense souvent à cette science et je tiens à laisser à la postérité une documentation aussi complète que possible… ».
                                <p className="project-quote-footer">Picasso</p></h2>

                            </div>
                        </div>
                        <h4>Le tableau et sa mosaïque</h4>
                        <p className="text-left content-project">Pour commencer, le tableau :</p>
                        <img className="articleIllustration" src={lexicrea_avignon_picasso} alt="demoiselle d'avignon picasso créativité" />
                        <p className="text-left content-project">La mosaïque :</p>
                        <img className="articleIllustration" src={lexicrea_img_mosaique} alt="demoiselle d'avignon picasso motivations créativité" />
                        <h5>Détail de la mosaïque</h5>
                        <p className="text-left content-project">Ce tableau a été peint environs 10 ans après l'invention des rayons X et du cinéma. Ces deux découvertes ont eu un écho particulier chez les peintres car les peintres avaient jusqu'alors l'illusion de partager un savoir du corps égal au scientifique ou au médecin.</p>
                        <p className="text-left content-project">L'artiste se trouve alors dépossédé de ses moyens et renvoyé à sa subjectivité.</p>
                        <p className="text-left content-project">Les champs de la psychologie s'élargissent également et ses conclusions sont désormais accessibles au plus grand nombre, portant la subjectivité comme thème majeur des productions artistiques à venir.</p>
                        <h6>Le cinéma</h6>
                        <p className="text-left content-project">Le cinéma c'est le mouvement des figures, Picasso y prend son idée de révéler une transformation des formes.</p>
                        <h6>La géométrie</h6>
                        <p className="text-left content-project">Picasso s'intéresse aux discours de Princet sur le mathématicien Poincaré. Il se focalise sur la simultanéité spatiale et le temps, celui de la 4ème dimension. Il dote son art d'un langage géométrique conceptuel qui explore la structure profonde des formes. La représentation, jusqu'alors informelle, se dote d'une rigueur formelle.</p>
                        <h6>Les rayons X</h6>
                        <p className="text-left content-project">Autre découverte de l'époque. Alors que les autres rayons allaient d'un coté à l'autre de l'espace, les rayons X ouvrent les corps, permettant à Picasso de réordonner ses éléments.</p>
                        <h6>La photographie</h6>
                        <p className="text-left content-project">La photographie s'utilise alors de plus en plus comme un appui à la peinture. Pour son tableau, Picasso s'inspira de texte plaçant la photographie comme un nouvel art.</p>
                        <h6>L'art africain</h6>
                        <p className="text-left content-project">L'Art africain vient confirmer à Picasso que l'usage de formes simples et géométrique permettait de toucher l'essentiel dans les représentations.</p>
                        <h6>Le complexe d'Œdipe</h6>
                        <p className="text-left content-project">Le complexe d'Œdipe du peintre et ses tentatives de castration envers son père. Il essayait d'y parvenir en devenant célèbre avec un style radical qui le séparait des autres artistes et des normes sociales.</p>
                        <h6>La compétition</h6>
                        <p className="text-left content-project">La visite du salon des indépendants, en 1907, où il put constater les progrès réalisés par Matisse et Derain anima chez Picasso le désir de les dépasser.</p>
                        <h6>Le sexe</h6>
                        <p className="text-left content-project">Le sexe était, avec le tabac et le travail, la grande addiction de Picasso. C'était le moteur de ses pulsions créatrices. Le choix d'un bordel comme le thème de ses expérimentations n'est donc pas un hasard...</p>
                        <h6>L’indépendance matérielle</h6>
                        <p className="text-left content-project">Après la vente de son studio et sa rencontre avec Kahn-weiler, Picasso est libéré de la nécessité matérielle et peut prendre son temps pour peindre les demoiselles ainsi que d'autres œuvres. Cela lui permet aussi de peindre des œuvres personnelles, sans la nécessité de séduire immédiatement des acheteurs.</p>
                        <h6>La philosophie</h6>
                        <p className="text-left content-project">La littérature et la philosophie lui donnent la force intellectuelle nécessaire pour surpasser les normes du moment et continuer de chercher une forme d'expression radicalement nouvelle.</p>
                        <h6>L'inspiration</h6>
                        <p className="text-left content-project">Picasso continue de s'inspirer de ses maîtres : Cézanne, El Greco ou Ingres. Mais aussi de ses contemporains, avec qui il entre directement en compétition.</p>

                    </div>
                </div>
                <div className="row align-center padding5_top">
                    <div className="small-11 medium-8 columns padding5_top">
                        <p className="letterCapitalcontent">M</p>
                        <h2>Motivations</h2>
                        <h3>Quelles Sont Les Motivations de la Créativité ?</h3>
                        <p className="text-left content-project">Selon l'approche multivariée, la créativité suppose la mise en marche de multiples motivations. Par motivations, nous entendons les raisons qui poussent un individu à commencer et finaliser une tâche créative.</p>
                        <div className="row align-center">
                        <div className="small-11 medium-9 columns">
                            <h2 className="project-quote">« … La phase créative échappe à toute dénomination, elle reste en dernière analyse un mystère indicible. Mais non point un mystère inaccessible incapable de nous ébranler jusqu’au tréfonds. Nous sommes chargés nous-mêmes de cette force jusqu’au dernier atome de moelle.
                                    Nous ne pouvons dire ce qu'elle est, mais nous pouvons nous rapprocher de sa source dans une mesure variable.
                                    Il nous faut de toute manière révéler cette force, la manifester dans ses fonctions tout comme elle se manifeste en nous.
                                    Elle est probablement matière elle-même, une forme de matière qui n'est pas perceptible aux mêmes sens que les autres espèces connues de matières.
                                    Mais il faut qu'elle se fasse connaître dans la matière connue. Incorporée à elle, elle doit fonctionner. Unie à la matière, elle doit prendre corps, devenir forme, réalité.
                                    ».
                                <p className="project-quote-footer">Paul Klee, théorie de l'Art moderne, 1945</p></h2>

                            </div>
                        </div>
                        <p className="text-left content-project">Klee fait ici état d'une motivation interne. La théorie psychanalytique de la créativité voit la genèse de la créativité dans le "conflit intérieur", résultat des émotions assoupies et qui se développent à travers des associations libres qui s'alimentent de l'imagination, du rêve et des jeux d'enfants. L'individu créatif accepterait et jouerait avec ces associations tandis que le non-créatif les rejetterait.</p>
                        <p className="text-left content-project">Les motivations pourraient s'ordonner en deux groupes : les motivations premières et les motivations secondaires. Les motivations premières engendrent les motivations secondaires.</p>
                        <h4>Premières motivations</h4>
                        <p className="text-left content-project">- L'énergie dérivée de la sublimation libidinale (idées de Freud, Lris, Bellak)
                        Pour illustrer cette hypothèse, voici un extrait du site de Serge Carfantan : « selon Freud, l’artiste est un névrosé qui parvient à sublimer ses pulsions sexuelles dans une création artistique. Toulouse Lautrec qui avait un physique disgracieux aurait compensé son complexe d’infériorité dans un choix de modèles de puissance et de grâce physique. Michel Ange peignait des colosses parce qu’il était lui-même malingre et chétif… Freud estime que l’artiste parvient à reprendre pied dans le réel, ce que le névrosé ordinaire ne peut faire. Il considère aussi que les artiste utilise l'Art pour exprimer des pulsions socialement non acceptable.
                        </p>
                        <p className="text-left content-project">Freud n’aborde pas le mystère de la création artistique comme moyen et don : « L’analyse ne peut en effet rien nous dire de relatif à l’élucidation du don artistique, et la révélation des moyens dont se sert l’artiste pour travailler, le dévoilement de la technique artistique n’est pas non plus de son ressort ». Carfantan continue en posant la névrose comme effet du talent et non la cause.</p>
                        <p className="text-left content-project">La motivation artistique selon Freud doit être prise en compte mais en aucun cas elle ne constitue la seule motivation de la création. Ce serait réduire l’importance de la sensibilité et du désir de communiquer. Continuons la liste des motivations mises en valeur par les spécialistes :</p>
                        <p className="text-left content-project">- La Curiosité (idées de Murphy, Thorndike, Rossman)</p>
                        <p className="text-left content-project">- L'élan d'actualisation/rénovation (Maslow, Rogers)</p>
                        <p className="text-left content-project">- La communication (Schachtel, Crutchfield, Irving Taylor, Fromm)
                        La valeur communication de l'oeuvre occupe une large place dans les motivations du créateur. Il s'agit d'émettre un message, de transmettre, de donner du sens. Cette communication sous entend aussi le rapport de l'artiste à la société (en donnant son oeuvre à contempler, il prend un risque et s'affirme en tant que sujet) à sa conception de l'éternité et de la mort (laisser une trace)
                        </p>
                        <p className="text-left content-project">- La réalisation de soi (Maslow, May)</p>
                        <p className="text-left content-project">- L'expression des émotions</p>
                        <p className="text-left content-project">- La réussite, l'argent
                        Il s'agit ici d'une motivation extérieure à l'individu. Elle peut influencer la motivation de l'individu mais selon quelques études elle ne semble pas être une motivation première pour la création artistique (Mc Graw, travaux d'Amabile).
                        </p>
                        <p className="text-left content-project">- La sublimation de la terreur (l'angoisse de la mort)</p>
                        <h4>Les motivations secondaires</h4>
                        <p className="text-left content-project">Les motivations secondaires sont celles qui dès leur origine étaient des manifestations générées par l'expérience des premières motivations.</p>
                        <p className="text-left content-project">- Plaisir dans l'acte de créer (Rossman, köhler, Suchman, Barron)</p>
                        <div className="row align-center">
                        <div className="small-11 medium-9 columns">
                            <h2 className="project-quote">Même tout ce qu'il n'est pas devient, malgré tout, pour l'homme une occasion de jouir de son être : dans l'art l'homme tire jouissance de se voir parfait.
                                <p className="project-quote-footer">Nietzsche, le crépuscule des idoles.</p></h2>

                            </div>
                        </div>
                        <p className="text-left content-project">Didier Anzieu disait très justement à ce sujet que « donner la vie à une oeuvre rassure, pendant le temps éphémère de l’inspiration sur sa propre toute-puissance, sur sa fécondité, sur son éternité : acte de foi narcissique, immense, mouvant comme la mer toujours recommencée entre les pins, entre les tombes... ».</p>
                        <p className="text-left content-project">- Goût pour la nouveauté (Maddi, Berne, Houston y Mednick)</p>
                        <p className="text-left content-project">- Élan vers la qualité (Maddi)</p>
                        <p className="text-left content-project">- Ordonner ses pensées. (Barron, Murphy)</p>
                        <p className="text-left content-project">- Ambition (Matussek, Tumin)</p>
                        <img className="articleIllustration" src={lexicrea_img_Illustr_mise_au_travail} alt="motivations de la créativité" />
                        <h4>La théorie existentialiste de la créativité</h4>
                        <p className="text-left content-project">La théorie existentialiste de la créativité défendue par Rolo May affirme que la créativité est une trouvaille. Par sa sensibilité et son esprit prédisposé, le peintre "créé" le cadre du paysage qu’il souhaite peindre. Il créé un cadre pour développer son jeu créatif. Alors que la théorie psychanalytique de la créativité limite l’explication de la créativité au champ de la sexualité, May affirme que l’individu créatif est ouvert à son environnement, il est donc plus réceptif et disposé à la trouvaille.</p>
                        <p className="text-left content-project">La créativité ne serait donc pas la satisfaction d’instincts réprimés mais la satisfaction d’une nécessité de communiquer avec l’environnement et de se réaliser.</p>
                        <p className="text-left content-project">Concluons cet article avec un extrait d'un article publié en avril 2013 dans "Frontiers in psychology":</p>
                        <div className="row align-center">
                        <div className="small-11 medium-9 columns">
                            <h2 className="project-quote">The creative activity of artists generally starts from the impulsion to create or make, to “do” or “incarnate”, and it is also fed by a curiosity to see and understand,to “find sensations”, and an need to express a narrative desire. Artists refer of ten to their work as a physical necessity and to its motivation as a form of internal pressure”.
                                <p className="project-quote-footer">Creativity as action : findings from five creative domains</p></h2>

                            </div>
                        </div>

                    </div>
                </div>

            </div>
            <p className="forward">
            <Link to="/N_livre_lexique_creativite_nature_et_creativite/">
                <p className="letterCapital">N</p>
                <p>Nature</p>
                <p>Nétotchka</p>
                <svg xmlns="http://www.w3.org/2000/svg" class="icon icon-tabler icon-tabler-arrow-narrow-right" width="44" height="44" viewBox="0 0 24 24" stroke-width="1" stroke="#282524" fill="none" stroke-linecap="round" stroke-linejoin="round">
                    <path stroke="none" d="M0 0h24v24H0z" fill="none"/>
                    <path d="M5 12l14 0" />
                    <path d="M15 16l4 -4" />
                    <path d="M15 8l4 4" />
                </svg>
            </Link>


        </p>
        </div>

    </div>






</PageTransition>
)

